.datt {
  border: 1px solid #eaeaea;
  padding: 1%;
  width: 100%;
  overflow: hidden;
}

.FullNews a {
  text-decoration: none;
  color: #000;
  font-size: 11px;

}

.FullNews i {
  border: 2px solid #000;
  padding: 5px;
  font-size: 15px;
  border-radius: 20px;
}

.ImgSection img {
  min-height: 220px;
  display: block;
  height: auto;
  -o-object-fit: contain;
  object-fit: contain;
  width: 100%;
}

.NewsContent img {
  min-height: 220px;
  display: block;
    height: auto;
    -o-object-fit: contain;
    object-fit: contain;
    width: 100%;
}

.FullNews .NewsContent h1 {

  overflow: hidden;

}

.NewsContent{
  font-size: 18px;
  line-height: 35px;
  margin: 10px 0;
  overflow: hidden;


}

.NewsContent h1 {
  font-size: 24px;
  line-height: 36px;
  margin-bottom: 10px;
  margin-top: 20px;
  font-weight: 600
}

/* .NewsContent p {
  font-size: 16px;
  padding: 10px 20px 10px 0px;
} */
.Newshedings h2 {
  color: #000;
  font-size: 20px;
  font-weight: 600;
}

.Newshedings p {
  font-weight: 500;
  padding: 10px;
}

.gu78 {
  color: #808080;
  line-height: 36px;
  font-size: 24px;
  font-weight: 700;
}

@media all and (max-width: 2000px) {
  .FullNews a {
    text-decoration: none;
    color: #000;
    font-size: 21px;
  }

  .FullNews i {
    border: 2px solid #000;
    padding: 8px;
    font-size: 20px;
    border-radius: 20px;
  }

