    font-weight: 500;
  } */
}

@media all and (max-width: 1800px) {
  .NewsContent h1 {
    font-size: 25px;
    margin: 10px 30px;
  }

  .NewsContent p {
    font-size: 21px;
    margin: 10px 30px;
    line-height: 33px;
    letter-spacing: 0px;
  }
}

@media all and (max-width: 1600px) {
  .NewsContent h1 {
    font-size: 21px;
    margin: 10px 30px;
  }

  .NewsContent p {
    font-size: 20px;
    margin: 10px 30px;
    line-height: 33px;
    letter-spacing: 0;
    margin: 10px;
    font-weight: 500;
  }
}

@media all and (max-width: 1300px) {
  .NewsContent h1 {
    font-size: 20px;
    margin: 10px 10px;
  }

  .FullNews a {
    text-decoration: none;
    color: #000;
    font-size: 17px;
  }

  .FullNews i {
    border: 2px solid #000;
    padding: 3px;
    font-size: 18px;
    border-radius: 20px;
  }
}

@media all and (min-width:360px) and (max-width:480px) {
  .NewsContent p {
    font-size: 18px;
    line-height: 30px;

}
}

